.burger {
  width: 24px;
  height: 16px;
  position: relative;
  cursor: pointer;
  display: inline-block;
  border: none;
  background-color: #ffffff;
  z-index: 10;
  margin-left: auto;
}
@media (min-width: 768px) {
  .burger {
    display: none;
  }
}
.burger__item {
  background-color: #000000;
  position: absolute;
  transition: 0.3s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  width: 100%;
  height: 2px;
  transition-duration: 500ms;
  top: -2px;
  left: 0;
}
.burger__item::after {
  content: "";
  background-color: #000000;
  position: absolute;
  transition: 0.3s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  width: 100%;
  height: 2px;
  transition-duration: 500ms;
  top: 16px;
  left: 0;
  opacity: 1;
}
.burger__item::before {
  content: "";
  background-color: #000000;
  position: absolute;
  transition: 0.3s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  width: 100%;
  height: 2px;
  transition-duration: 500ms;
  top: 8px;
  left: 0;
}
.burger.open .burger__item {
  transform: rotate(45deg);
  top: 6px;
}
.burger.open .burger__item::before {
  opacity: 0;
}
.burger.open .burger__item::after {
  transform: rotate(-90deg);
  top: 0;
}
